.friends{
    list-style: none;

    margin: 0 auto;
    padding: 0;

    display: flex;
    flex-direction: row;
    gap: 10px;

}

.item{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
    
    border-radius: 4px;
    border: 1px solid #f0f1f0;
    background-color: #f5faf7;
    
    margin-top: 20px;
    padding: 10px;
}

.avatar{
    margin: 0 auto;
}

.name{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 700;
    font-size: 20px;
    color: dimgray;
}

.online{
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: green;    
}

.offline {
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
}